<template>

  <div class="range">
    <span  class="rangeValue" v-bind:class="{ datashow: isActive}">{{ rangeNum }}</span>
    <input class="rangeSlider"
           type="range" name="points"
           min="0" max="100"
           v-model="rangeNum"
           v-on:mouseLeave="dataShow"
           v-on:mouseover="dataShow"
           v-bind:style="{ background: FillValue }">
  </div>

</template>

<script>
/* eslint-disable */
export default {
  props: {
    color: {
      type: String,
      default: '',
    },
    fill: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      rangeNum: 0,
      isActive: false,
      filltype: '',
    };
  },
  computed: {
    FillValue() {
      let fillcolor;
      // console.log(this.fill === true, this.color);
      if (this.fill === true) {
        fillcolor = 'linear-gradient(90deg, ' + this.color + ' 0%, ' + this.color + ' ' + this.rangeNum + '%, #ccc ' + this.rangeNum + '%, #ccc)';
      } else {
        fillcolor = this.color;
      }
      // console.log(this.fill === true, fillcolor);
      return fillcolor;
    },
  },
  // attached () {},
  methods: {
    dataShow() {
      this.isActive = !this.isActive;
    },
  },
  components: {},
};
/* eslint-enable */
</script>

<style lang="scss">
@import "../scss/app.scss";

</style>



