<template>
      <!-- <button v-for="d in className" class="button {{ d.type }}">
        {{ d.content }}
      </button> -->

      <button class="button" v-bind:style="{ borderColor: color, color: color }">
        custom
      </button>
      <button class="button" v-bind:style="{ borderColor: btncolor2, color: btncolor2 }">
        custom
      </button>
      <button class="button" v-bind:style="{ borderColor: btncolor3, color: btncolor3 }">
        custom
      </button>
      <button class="button" v-bind:style="{ borderColor: btncolor4, color: btncolor4 }">
        custom
      </button>
      <button class="button" v-bind:style="{ borderColor: btncolor5, color: btncolor5 }">
        custom
      </button>

</template>

<script>
/* eslint-disable */
import colorPicker from 'color';
export default {

  props: {
    color: '',
  },
  data() {
    return {
      styleObject: {
        borderColor: '',
        btncolor2: '',
        btncolor3: '',
        btncolor4: '',
        btncolor5: '',
      },
      className: [
        {
          type: 'button-default',
          content: 'simple',
        },
        {
          type: 'button-active',
          content: 'Great!',
        },
        {
          type: 'button-warning',
          content: 'woops?',
        },
        {
          type: 'button-danger',
          content: 'Fuck!',
        },
        {
          type: 'button-disable',
          content: 'NO WAY',
        },
      ],
    };
  },
  computed: {
    btncolor2: function() {
      // console.log(this.color);
      // this.btncolor3 = colorPicker(this.color).darken(0.40).rgbString();
      // console.log(colorPicker(this.color).darken(0.20).rgbString());
      return colorPicker(this.color).darken(0.20).rgbString();
    },
    btncolor3: function() {
      return colorPicker(this.color).darken(0.40).rgbString();
    },
    btncolor4: function() {
      return colorPicker(this.color).darken(0.60).rgbString();
    },
    btncolor5: function() {
      return colorPicker(this.color).darken(0.80).rgbString();
    },
  },
};
/* eslint-enable */
</script>

<style scoped lang='scss'>
@import "../scss/app.scss";
</style>



