<template>
  <div class="table-wrapper" v-bind:class="{ border: isBorder , card: true, horizon: isHorizon}">
    <table id="flip-scroll">
     <thead>
      <tr>
       <th v-for="label in labels" v-bind:style="{ background:color }">{{ label }}</th>
      </tr>
     </thead>
     <tbody>
      <tr v-for='dd in datas' v-bind:style="{ background: $index%2 == 1? rowColor : '' }">
        <td v-for='d in dd'>{{ d }}</td>
      </tr>
     </tbody>
    </table>
  </div>
</template>

<script>
/* eslint-disable */
import colorPicker from 'color';
export default {

  props: {
    color: '',
    isBorder: '',
    isCard: '',
    isHorizon: '',
  },
  data() {
    return {
      labels: ['Nick', 'Mail', 'Web', 'Twitter', 'Id'],
      datas: [
        ['gFulanito', 'gFulanito', 'fuggleanito.com', 'Fulagnito', 20],
        ['Fulanieto', 'Fuelanito', 'fulanito.com', 'Fulanito', 14],
        ['Keolor', 'tocdrfisw', 'ddgr.com', '20wewe', 100],
        ['qwr', 'aaasrfisw', 'ddewd.com', '20wewe', 12],
        ['raor', 'ewrfisw', 'fed.com', '20wewe', 40],
      ],
      rowColor: '',
    };
  },
  computed: {
    rowColor: function() {
      console.log(this.isHorizon);
      return colorPicker(this.color).lighten(0.75).rgbString();
    },
  },
  // ready () {},
  // attached () {},
  // methods: {},
  // components: {},
};
/* eslint-enable */
</script>

<style scoped lang="scss">
@import "../scss/app.scss";


</style>



