<template>
   <!-- <div class="radio" v-bind:class="{ border: border }">
     <input type="radio" v-bind:id.once='id' v-model='name' readonly=''
            :value="value"
            :checked="checked"
            name='name'>
     <label v-bind:for.once='id'
            v-bind:style="{ 'border-color': borderValue }">
            <i v-show="round"></i>{{ value }}
     </label>
   </div> -->

   <label class="rad">
     <input type="radio" name="r1" value="value" />
     <i v-bind:style="{ 'background': borderValue, 'border-color': borderValue}"></i>
     {{ labels }}
   </label>

</template>

<script>
/* eslint-disable */
export default {
  data () {
    return {
      name: '',
      border: true,
    }
  },
  methods: {

  },
  computed: {
    borderValue() {
      let borderType;
      if (this.border === true) {
        borderType = this.color;
      } else {
        borderType = '';
      }
      return borderType;
    },
  },
  props: {
    labels: '',
    id: {
      required: true
    },
    value: {
      required: false
    },
    // options: {
    //   required: true
    // },
    color: '',
    border: false,
    round: false,
  },
  ready () {
  },
  watch: {
  }
}
/* eslint-enable */
</script>

<style lang="scss">
@import "../scss/app.scss";
  .radio {
    // cursor: pointer;
    -webkit-user-select: none;
    user-select: none;

    input[type="radio"] {
      -webkit-appearance: none;
              appearance: none;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 2px solid $color-border;
      background: $color-border;
      cursor: pointer;
      transition: 0.35s ease;
      box-shadow: inset 0 0 0 8px #fff;

      &:hover {
        box-shadow: inset 0 0 0 3px #fff;
        background: $color-border;
      }

      &:checked + label{
        box-shadow: inset 0 0 0 3px #fff;
        background: orange;
      }
    }
    &.border {

      input[type="radio"] {
        display: none;
      }

      label {
        cursor: pointer;
        font-size: 1.25em;
        display: inline-block;
        border-radius: 30px;
        // box-shadow: 0px 0px 0px 0px pink;
        border: 0;
        padding: 0.25rem 1.25rem;
        margin: 0.5rem 0.5rem;
        background: transparent;
        transition: all 0.35s ease;

      }

      input[type="radio"]:checked + label {
          transition: all 0.25s ease;
          border-width: 3px;
      }

    }
  }



  /* CUSTOM RADIO & CHECKBOXES
     http://stackoverflow.com/a/17541916/383904 */
  .rad,
  .ckb{
    cursor: pointer;
    user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
  }
  .rad > input,
  .ckb > input{ /* HIDE ORG RADIO & CHECKBOX */
    visibility: hidden;
    position: absolute;
  }
  /* RADIO & CHECKBOX STYLES */
  .rad > i,
  .ckb > i{     /* DEFAULT <i> STYLE */
    display: inline-block;
    vertical-align: middle;
    width:  20px;
    height: 20px;
    border-radius: 50%;
    transition: 0.2s;
    box-shadow: inset 0 0 0 8px #fff;
    border: 2px solid gray;
    background: gray;
  }
  /* CHECKBOX OVERWRITE STYLES */
  .ckb > i {
    width: 25px;
    border-radius: 3px;
  }
  .rad:hover > i{ /* HOVER <i> STYLE */
    box-shadow: inset 0 0 0 3px #fff;
  }
  .rad > input:checked + i{ /* (RADIO CHECKED) <i> STYLE */
    box-shadow: inset 0 0 0 3px #fff;
  }
  /* CHECKBOX */
  .ckb > input + i:after{
    content: "";
    display: block;
    height: 12px;
    width:  12px;
    margin: 2px;
    border-radius: inherit;
    transition: inherit;
    background: gray;
  }
  .ckb > input:checked + i:after{ /* (RADIO CHECKED) <i> STYLE */
    margin-left: 11px;
    background:  orange;
  }
</style>



