<template>
  <div id="app" v-cloak>
    <Navbar></Navbar>
    <div class="demo-wrapper">
      <input class="temp" type="text" name="color" value="" v-model="colorController">

      <Typography></Typography>

      <Upload></Upload>

      <div class="Select demo-section">
        <div class="demo-header" >
          <h1>Kolor-select</h1>
        </div>
        <select v-model="colorController">
          <option v-for="option in kolorOptions">{{ option }}</option>
        </select>
      </div>

      <div class="Upload demo-section">
        <div class="demo-header" >
          <h1>Loader</h1>
        </div>
        <div class="demo-text">
          <Loader size='70px' :color='colorController' type='bg'></Loader>
        </div>
      </div>

      <div class="Button demo-section">
        <div class="demo-header">
          <h1>Buttons</h1>
        </div>
        <div class="demo-text">
          <Button :color='colorController'></Button>
        </div>
      </div>

      <div class="Grid demo-section">
        <div class="demo-header">
          <h1>Grid</h1>
        </div>

        <div class="demo-text">
          <Grid :color='colorController'></Grid>
        </div>
      </div>

      <div class="Table demo-section">
        <div class="demo-header">
          <h1>Table</h1>
        </div>

        <div class="demo-text">
          <k-table :color='colorController' :is-border='false' :is-horizon='true'></k-table>
        </div>
      </div>

      <div class="Range demo-section">
        <div class="demo-header">
          <h1>Range</h1>
        </div>

        <div class="demo-text">
          <!-- <k-input-radio :color='colorController' :fill=true></k-input-radio> -->
          <k-input-range :color='colorController' :fill='false'></k-input-range>
          <k-input-range :color='colorController' :fill='true'></k-input-range>
        </div>
      </div>


      <div class="Radio demo-section">
        <div class="demo-header">
          <h1>Radio</h1>
        </div>

        <div class="demo-text">
          <form>
            <k-input-radio name="subscription-pro" :color='colorController'
                           :checked.sync='subscription' id="select-man" value='Men'
                           labels="mazkrov" :border='true'>
                           Men</k-input-radio>
            <k-input-radio name="subscription-pro" :color='colorController'
                           :checked.sync='subscription' id="select-women" value='Women'
                           labels='kelly sint' :border='true'>
                           Women</k-input-radio>
          </form>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar';
import Typography from './components/Typography';
import Button from './components/Button';
import Upload from './components/UploadContainer';
import Loader from './components/Loader';
import Grid from './components/Grid';
import kTable from './components/Table';
import kInputRange from './components/InputRange';
import kInputRadio from './components/InputRadio';

export default {
  data() {
    return {
      kolorOptions: ['#E95859', '#F8B55D', '#61AF8B', '#2EA7D9'],
      colorController: '#50514f',
      subscription: true,
    };
  },
  components: {
    Navbar,
    Typography,
    Button,
    Upload,
    Loader,
    Grid,
    kTable,
    kInputRange,
    kInputRadio,
  },
};
</script>

<style lang='scss'>
@import "./scss/app.scss";

#app {
  color: $color-default;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
}

.logo {
  width: 100px;
  height: 100px
}

.temp {
  margin: 20px 0 50px 0;
  padding: 5px 10px;
  border-radius: 5px;
  border: 3px solid $color-default;
}
</style>



