<template>

      <div class="row">
        <div class="col-xs-12">
          <div class="box" v-bind:style="{ background: color }">1</div>
        </div>
      </div>

      <div class="row reverse">
        <div class="col-xs-6">
          <div class="box" v-bind:style="{ background: gridcolor2 }">1</div>
        </div>
        <div class="col-xs-6">
          <div class="box" v-bind:style="{ background: gridcolor2 }">2</div>
        </div>
      </div>

      <div class="row">
        <div class="col-xs-3">
          <div class="box" v-bind:style="{ background: gridcolor3 }">1</div>
        </div>
        <div class="col-xs-3">
          <div class="box" v-bind:style="{ background: gridcolor3 }">2</div>
        </div>
        <div class="col-xs-3">
          <div class="box" v-bind:style="{ background: gridcolor3 }">3</div>
        </div>
        <div class="col-xs-3">
          <div class="box" v-bind:style="{ background: gridcolor3 }">4</div>
        </div>
      </div>

      <div class="row">
        <div class="col-xs-6 col-xs-offset-3">
          <div class="box" v-bind:style="{ background: gridcolor4 }">offset-3</div>
        </div>
      </div>

      </div>
</template>

<script>
import colorPicker from 'color';
export default {
  props: {
    color: '',
  },
  data() {
    return {
      styleObject: {
        borderColor: '',
        btncolor2: '',
        btncolor3: '',
      },
    };
  },
  computed: {
    gridcolor1() {
      return colorPicker(this.color).darken(0.10).rgbString();
    },
    gridcolor2() {
      return colorPicker(this.color).darken(0.20).rgbString();
    },
    gridcolor3() {
      return colorPicker(this.color).darken(0.30).rgbString();
    },
    gridcolor4() {
      return colorPicker(this.color).darken(0.40).rgbString();
    },
  },
};
</script>

<style scoped lang="scss">
@import "../scss/app.scss";

.demo-text {
  width: 100%;
}

.row {
  margin: 1rem auto;
}

.col-xs-3,
.col-xs-6,
.col-xs-12 {

  .box {
    height: 50px;
    width: 100%;
    border-radius: 5px;
    border: 3px solid darken($color-white, 60);
    @include flexStyle(row, center, center);
  }
}

.col-xs-12 {
  .box {
    background: $color-blue;
  }
}
.col-xs-6 {
  .box {
    background: $color-yellow;
  }
}
.col-xs-3 {
  .box {
    background: $color-red;
  }
}
</style>



